<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process116"></a>

<p><br></p>
<p><span class="rvts17">欢乐堡竞技场（</span><span class="rvts50">The Arena of FortJoy</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:210,Y:134</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：努西（</span><span class="rvts20">Noosey</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">升级爆装备</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1024.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">竞技场实际上就在欢乐堡厨房去旁边，和</span><span class="rvts20">NPC</span><span class="rvts21">努西对话，变会找到她身旁的暗门。下了暗门楼梯，里面就是竞技场。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1025.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">竞技场只有一次战斗，无论你是输是赢，机会只有一次。赢得比赛可以获得额外装备奖励和经验奖励。</span>
<br><span class="rvts21">另外在战斗过程中，战场上也有很多宝箱可以打开进行搜刮。但是，一旦赢了比赛，或输了比赛，就不能再次进入竞技场。</span>
<br><span class="rvts21">如果赢得了比赛，那么主角脖子上的源力束缚环将被取下（其余</span><span class="rvts20">3</span><span class="rvts21">名队友的除外）。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



